@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primary:#42b5a4;
  --secondary: #ffe286;
  --correct:hsl(153, 98%, 42%);
  --error:#e94f4f;

  background-color: #42b5a4;

  background-color: var(--primary);
  font-family: 'Quicksand', sans-serif; 
}


.App{
  width: 100%;
  max-width: 380px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  margin: 15px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px 10px;
}

.container{
  height: 400px;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  flex-direction: column;
}

.score{
  color: white;
  font-size: 34px;
}
.start-again{
  width: 350px;
  background-color: white;
  color: #42b5a4;
  color: var(--primary);
  border: 1px solid #42b5a4;
  border: 1px solid var(--primary);
  border-radius: 5px;
  padding: 10px 0;
  font-family: 'Quicksand', sans-serif; 
  font-weight: 700;
  cursor: pointer; 
  transition: all 0.2s linear;
  margin: 5px auto;
  box-shadow: 0px 3px 3px -2px rgba(153,153,153,0.25);
}

.start-again:hover{
  background-color: #42b5a4;
  background-color: var(--primary);
  color: white;
  border: 1px solid white;
  outline: unset;
}


.footer{
  margin-top: 20px;
  margin-bottom: 0px;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  text-align: center;
}
.button{
    width: 100%;
    min-width: 100%;
    color: var(--primary);
    background-color: transparent;
    border: 1px solid var(--primary);
    border-radius: 5px;
    padding: 10px 0;
    font-family: 'Quicksand', sans-serif; 
    font-weight: 700;
    cursor: pointer; 
    transition: all 0.2s linear;
    margin-bottom: 10px;
    box-shadow: 0px 3px 3px -2px rgba(153,153,153,0.25);
}

.button:hover{
    background-color: var(--primary);
    color: white;
    border: 1px solid white;
    outline: unset;
}
.question{
    width: 100%;
    min-width: 100%;
    background-color: var(--secondary);
    border-radius: 5px;
    font-family: 'Quicksand', sans-serif; 
    font-weight: 700;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 3px 3px -2px rgba(153,153,153,0.25);
    padding: 10px;
    box-sizing: border-box;
    margin-bottom: 20px;
    word-wrap: break-word;
}

.question header{
    color: var(--secondary);
    background-color: white;
    padding: 5px;
    border-radius: 5px;
    margin-top: -20px;
    margin-bottom: 20px;
    border: 1px solid var(--secondary);
}

.question footer{
    color: var(--secondary);
    background-color: white;
    padding: 5px;
    border-radius: 5px;
    margin-top: 20px;
    margin-bottom: -20px;
    border: 1px solid var(--secondary);
}


.load-container{
  height: 400px;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
